// –í–µ—Ä—Å–∏—è —Ç—É–ª—Ç–∏–ø–æ–≤ –Ω–∞ jUqery UI. –ù–µ–∞–∫–∫—É—Ä–∞—Ç–Ω–æ —Ä–∞–±–æ—Ç–∞–µ—Ç –Ω–∞ –∞–π–ø–∞–¥–µ, –¥–∞ –∏ –≤–æ–æ–±—â–µ.
import tippy from 'tippy.js';

export default () => {
  // –ü–æ–¥—Å–∫–∞–∑–∫–∏/Tooltips –¥–ª—è –≤ –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä–µ –ë—é–¥–∂–µ—Ç–∞
  const tooltips = document.querySelectorAll('.js-tooltip');
  const screenWidth = () => window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth || 0;
  const tooltipPosition = (screenWidth() > 559) ? 'right' : 'top';

  if (tooltips) {
    tippy('.js-tooltip', {
      // @TEST
      // trigger: 'click',
      delay: [10, 100],
      performance: true,
      animation: 'shift',

      position: tooltipPosition,
      offset: 20,
      // animation: 'perspective',
      duration: 200,
      arrow: true,
      distance: 20,
      theme: 'light',
      size: 'big',
      arrowSize: 'big',
      popperOptions: {
        modifiers: {
          flip: {
            behavior: ['right', 'bottom']
          }
        }
      }
    });
  }
}


