import $ from 'jquery';

export default function toppanel() {
  // –°–∫—Ä—ã—Ç—å/–ø–æ–∫–∞–∑–∞—Ç—å —Ç–æ–ø–ø–∞–Ω–µ–ª—å –î–ò–¢–∞
  $('a[data-toggle-toppanel="data-toggle-toppanel"]').on('click', function(e) {
    e.preventDefault();
    $('.top-panel-frame-wrp').toggleClass('is-active');
    $('#header').toggleClass('toppanel-is-active');
  });

  // –°–∫—Ä—ã–≤–∞–µ–º —Ç–æ–ø–ø–∞–Ω–µ–ª—å –î–ò–¢ –ø—Ä–∏ —Å–∫—Ä–æ–ª–ª–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤–Ω–∏–∑
  // –≤ —Ñ–∞–π–ª–µ app/components/header/header.js

  // –°–∫—Ä—ã–≤–∞–µ–º —Ç–æ–ø–ø–∞–Ω–µ–ª—å –î–ò–¢ –ø—Ä–∏ –∫–ª–∏–∫–µ –∑–∞ –µ—ë –ø—Ä–µ–¥–µ–ª–∞–º–∏
  $('body').on('click', function(e) {
    if (!$(e.target).closest('a[data-toggle-toppanel="data-toggle-toppanel"]').length && !$(e.target).closest('.top-panel-frame-wrp').length) {
      $('.top-panel-frame-wrp').removeClass('is-active');
      $('#header').removeClass('toppanel-is-active');
    }
  });
}



